body {
  background: #141414;
  color: #ffffff;
}

.image-container {
  position: relative;
  transition: transform 0.2s;
}

.image-container:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.image-container:hover .overlay {
  opacity: 1;
}

.overlay {
  position: absolute;
  width: 100%;
  transition: 0.5s ease;
  opacity: 0;
  bottom: 0;
  margin-top: 40px;
  font-size: 20px;
  padding: 20px;
  text-align: center;
}
